@import "https://fonts.googleapis.com/css2?family=Lato:wght@100;400;700&display=swap";
:root {
  --accent-color: #bbf7d0;
  --accent-color-dark: #125329;
  --secondary-accent-color: #6b21a8;
  --secondary-accent-light-color: #f3e8ff;
  --bg-color: #f3f4ff;
  --bg-color-dark: #c6c8dc;
  --card-bg-color: #fff;
  --font-color: #2f2f2f;
  --loading-color: #dee0f3;
  --loading-accent: #9ba1d2;
  --list-item-bg-color: #fff;
  --border-radius: 5px;
  --border-radius-lrg: 10px;
  --default-transition: all .2s ease-in;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 16px;
}

body {
  background-color: var(--bg-color);
  color: var(--font-color);
  min-height: 100vh;
  font-family: Lato, Arial, Helvetica, sans-serif;
  font-weight: 400;
  line-height: 1.5;
  display: flex;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: 700;
}

a {
  border-bottom: 2px dotted var(--secondary-accent-color);
  color: var(--secondary-accent-color);
  transition: var(--default-transition);
  text-decoration: none;
}

a:hover {
  color: var(--font-color);
  border: 0;
}

ul {
  padding-left: 0;
  list-style: none;
}

.nav, .app__body, .app__footer {
  padding: 1.5rem;
}

.app__header {
  border-bottom: 1px solid #c1cecf;
  margin: 0 1.5rem;
  padding: 1.5rem 0;
}

.app__body--dashboard {
  grid-template-rows: repeat(2, min-content);
  grid-template-columns: repeat(3, 1fr);
  gap: .75rem;
  display: grid;
}

.app, .app__header, .app__main, .app__footer {
  display: flex;
}

.app, .app__main {
  flex-grow: 1;
}

.app__header, .app__footer {
  justify-content: space-between;
}

.app__main {
  flex-direction: column;
}

.app__body {
  flex-grow: 1;
  position: relative;
}

.app__body--map {
  flex-direction: column;
  display: flex;
}

.app__footer {
  text-align: right;
}

.nav {
  background-color: var(--card-bg-color);
}

.nav__list {
  flex-direction: column;
  gap: 1.5rem;
  display: flex;
}

.nav__item {
  background-color: var(--bg-color);
  border-radius: var(--border-radius);
  cursor: pointer;
  text-align: center;
  transition: var(--default-transition);
  padding: .75rem;
  font-size: 1.3rem;
}

.nav__item:hover {
  background-color: var(--bg-color-dark);
}

.stats__wrapper {
  justify-content: space-between;
  display: contents;
}

.stats__card {
  background-color: var(--card-bg-color);
  border-radius: var(--border-radius-lrg);
  flex-grow: 1;
  grid-template-rows: repeat(auto-fill, minmax(50px, 1fr));
  grid-template-columns: 1fr 60px;
  padding: 1rem 1.5rem;
  display: grid;
}

.stats__card__title {
  opacity: .8;
  grid-area: 2 / 1 / -1 / 1;
  font-size: 1.1rem;
  font-style: italic;
  font-weight: 400;
}

.stats__card__value {
  grid-area: 1 / 1 / 2 / 1;
  font-size: 2.4rem;
  font-weight: 700;
}

.stats__card__icon {
  background-color: var(--accent-color);
  color: var(--accent-color-dark);
  border-radius: 14px;
  grid-area: 1 / 2 / 3 / -1;
  place-self: center;
  padding: .75rem;
  font-size: 2.1rem;
}

.loading {
  border: 6px solid var(--loading-color);
  border-top: 7px solid var(--loading-accent);
  border-radius: 100%;
  width: 3rem;
  height: 3rem;
  margin: auto;
  animation: 1s linear infinite spin;
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loading.display {
  display: inline-block;
}

.view__title {
  text-transform: uppercase;
  justify-content: space-between;
  margin-bottom: 1.5rem;
  display: flex;
}

@keyframes spin {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

.table__header {
  margin-bottom: .75rem;
}

.table__body {
  flex-direction: column;
  gap: .75rem;
  display: flex;
}

.table__header, .list__item {
  background-color: var(--list-item-bg-color);
  border-radius: var(--border-radius);
  box-shadow: 0;
  transition: var(--default-transition);
  grid-template-columns: 40px repeat(auto-fit, minmax(80px, 1fr));
  align-items: center;
  gap: .75rem;
  padding: .75rem;
  display: grid;
}

.list__item:hover {
  box-shadow: 0 0 6px #c6c8dce6;
}

.list__item__icon {
  background-color: var(--secondary-accent-light-color);
  border-radius: var(--border-radius);
  color: var(--secondary-accent-color);
  text-align: center;
  height: 40px;
  line-height: 40px;
}

.list__item__coordinates {
  flex-direction: column;
  gap: .1875rem;
  display: flex;
}

.list__item__coordinates span {
  font-size: 13px;
}

.list__item__lat:before, .list__item__long:before {
  content: "Lat.";
  margin-right: .375rem;
  font-weight: 700;
}

.list__item__long:before {
  content: "Lon.";
}

.panel {
  background-color: var(--list-item-bg-color);
  border-radius: var(--border-radius-lrg);
  padding: 1.5rem;
}

.panel--dashBoardChart {
  grid-area: 2 / 1 / 3 / 3;
}

.panel--lastObservations {
  grid-area: 2 / 3 / 3 / 3;
}

.panel .list__item {
  padding: .75rem 0;
}

.panel .list__item:hover {
  box-shadow: none;
}

.panel--lastObservations .list__item__date, .panel--lastObservations .list__item__species {
  font-size: 14px;
  font-weight: 400;
}

.map {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

